.spinner {
  line-height: 1.625rem;
}
.spinner .ballDecorator {
  border-radius: 1.5rem;
  position: relative;
  width: 3rem;
  height: 3rem;
  margin: 0 auto;
}
.spinner .ball {
  position: absolute;
  text-align: center;
  font-size: 90%;
  line-height: 0.4em;
  -webkit-animation: none 1.25s linear infinite;
  animation: none 1.25s linear infinite;
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
  height: 15%;
  width: 15%;
  padding: 0 1px;
  left: 42.5%;
  bottom: 15%;
  -webkit-transform-origin: center -133.33333333%;
  transform-origin: center -133.33333333%;
}
.spinner .ball::after {
  content: "\0EFFDB";
  font-family: "Moonstone Icons";
}
.spinner .ball1 {
  -webkit-animation-name: spinBall;
  animation-name: spinBall;
  -webkit-transform: rotateZ(13deg);
  transform: rotateZ(13deg);
}
.spinner .ball2 {
  -webkit-animation-name: spinBall2;
  animation-name: spinBall2;
  -webkit-transform: rotateZ(347deg);
  transform: rotateZ(347deg);
}
.spinner .ball3 {
  -webkit-animation-name: spinBall3;
  animation-name: spinBall3;
  -webkit-transform: rotateZ(321deg);
  transform: rotateZ(321deg);
}
.spinner .client {
  font-family: "MuseoSans";
  font-weight: 300;
  font-size: 1.375rem;
  line-height: 1.625rem;
  font-weight: bold;
  margin-top: 0.5rem;
  max-width: 14.5rem;
}
:global(.enact-locale-non-latin) .spinner .client {
  font-family: "Moonstone LG Display";
  font-weight: 300;
  font-size: 1.125rem;
  line-height: 1.7em;
}
.spinner .client a:link {
  color: inherit;
  text-decoration: none;
}
.spinner .client a:visited {
  color: inherit;
  text-decoration: none;
}
.spinner .client a:hover {
  color: inherit;
  text-decoration: none;
}
.spinner .client a:active {
  color: inherit;
  text-decoration: none;
}
.spinner.content {
  padding: 0.25rem;
}
.spinner.running .ball {
  -webkit-animation-play-state: running;
  animation-play-state: running;
}
